const convert = (items: Array<IPageTemplate>) => {
    const seperateToGroups = (targetItems: Array<IPageTemplate>, field: "category" | "subCategory"): Record<string, IPageTemplate[]> => {
        const groups: Record<string, Array<IPageTemplate>> = targetItems.reduce((group, item) => {
            const category = item[field];
            const grp = group as any;
            grp[category] = grp[category] ?? [];
            grp[category].push(item);
            return grp;
        }, {});
        return groups;
