    const retVal: Array<ITemplateCategoryItem> = [];
    const categoryGroups = seperateToGroups(items, "category");
    Object.keys(categoryGroups).forEach((groupName) => {
        const subGroupItems = templates.value.filter((i) => i.category == groupName);
        const subGroups = seperateToGroups(subGroupItems, "subCategory");
        const children: Array<ITemplateCategoryItem> = Object.keys(subGroups).map((i) => {
            const subGroup = subGroups[i];
            const subCategoryName = subGroup[0].subCategory;
            const pageItems = subGroupItems.filter((i) => i.subCategory == subCategoryName);
            const subCategory: ITemplateCategoryItem = {
                name: subCategoryName,
                type: "category",
                children: pageItems.map((page) => {
                    return <IPageItem>{ ID: page.ID, name: page.name, type: "page", image: page.image };
                }),
            };
            return subCategory;
        });
        retVal.push({ name: groupName, type: "category", children });
    });

