ar {\n    background: #FFFFFF;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/create/createNewScreen.vue"],"names":[],"mappings":";AAwPA;IACI,qBAAqB;AACzB;AAEA;IACI,kBAAkB;IAClB,2BAA2B;IAC3B,iCAAiC;AACrC;AAEA;IACI,4BAA4B;AAChC;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,qBAAqB;IACrB,aAAa;AACjB;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,mBAAmB;IACnB,kBAAkB;IAClB,WAAW;AACf;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;AACtB;;AAEA;;;;;;;;;;;GAWG;AAEH;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;AACpB;AAEA;IACI,WAAW;IACX,yBAAyB;IACzB,kBAAkB;IAClB,uBAAuB;IACvB,iBAAiB;AACrB;AAEA;IACI,8BAA8B;IAC9B,0BAA0B;AAC9B;AAEA;IACI,mBAAmB;IACnB,cAAc;IACd,eAAe;IACf,uBAAuB;IACvB,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;IAClB,kBAAkB;AACtB;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,cAAc;IACd,uBAAuB;IACvB,YAAY;IACZ,iBAAiB;AACrB;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,YAAY;IACZ,mBAAmB;IACnB,YAAY;IACZ,gBAAgB;AACpB;AAEA;IACI,wBAAwB;IACxB,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,yBAAyB;IACzB,cAAc;IACd,yBAAyB;AAC7B;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,2CAA2C;IAC3C,cAAc;IACd,YAAY;IACZ,kBAAkB;AACtB;AACA;IACI,cAAc;AAClB;AACA;IACI,aAAa;IACb,mBAAmB;IACnB;AACJ;AACA;IACI,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,YAAY;IACZ,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;IACf,6BAA6B;AACjC;AACA;IACI,yBAAyB;IACzB;AACJ;AACA;IACI,0BAA0B;IAC1B,qBAAqB;IACrB,mBAAmB;AACvB;AACA;IACI,mBAAmB;IACnB,mBAAmB;IACnB,yBAAyB;IACzB,4BAA4B;AAChC;AACA;IACI,mBAAmB;AACvB;AACA;IACI,mBAAmB;AACvB","sourcesContent":["<template>\n    <div class=\"dialog-container\">\n        <div class=\"input-label\">UI Page Name</div>\n        <IDEInput :isRequired=\"true\" elementId=\"nameInput\" :disable-required-icon=\"true\" @getValue=\"setNameValue\"\n            @validateStatus=\"setNameValidationStatus\" placeholder=\"\" :type=\"'text'\" info-text=\"\"\n            :validations=\"validations.name\" />\n        <hr style=\"margin: 32px 0px 32px 0px;\" />\n        <div class=\"template-tabs\">\n            <div @click=\"selectTemplateCategory('Web')\" :class=\"showWebTemplates ? 'active-category' : ''\">Web</div>\n            <div @click=\"selectTemplateCategory('Mobile')\" :class=\"showMobileTemplates ? 'active-category' : ''\">Mobile</div>\n        </div>\n        <div style=\"margin-top: 32px\">\n            <div style=\"display: flex\">\n                <div class=\"template-categories\">\n                    <button v-if=\"items.length > 0\"\n                        :class=\"{ 'button-text-container': true, active: selectedTabName[selectedTemplateCategory] == 'BLANK_PAGE' }\"\n                        @click=\"changeMainTab('BLANK_PAGE')\">\n                        Blank Page\n                    </button>\n                    <div v-for=\"subCategory in selectedItems\" :key=\"subCategory.name\">\n                        <button :class=\"{ 'button-text-container': true, active: subCategory.name == selectedTabName[selectedTemplateCategory] }\"\n                            @click=\"changeMainTab(subCategory.name)\">\n                            {{ subCategory.name }}\n                        </button>\n                    </div>\n                </div>\n                <div v-if=\"items.length > 0\" class=\"border-center\"></div>\n                <div class=\"template-container\">\n                    <div class=\"template-card-container\" v-if=\"selectedTabName[selectedTemplateCategory] == 'BLANK_PAGE' && items.length > 0\">\n                        <div :class=\"{ 'page-button-container': true, active: activeTemplateID == 'BLANK_PAGE' }\"\n                            @click=\"changeActiveTemplateID('BLANK_PAGE')\">\n                            <img src=\"./../../../../../images/blank_page.svg\" class=\"blank-page\" alt=\"Blank page\"/>\n                        </div>\n                        <p class=\"template-small-header\"> Blank Page </p>\n                    </div>\n                    <div class=\"template-card-container\" \n                        v-for=\"(child, index) in flattener(selectedTabName[selectedTemplateCategory])\"\n                        :key=\"index\"\n                        :style=\"(index + 1) % 3 == 0 ? 'margin-right: 0px' : ''\"\n                        >\n                        <div :class=\"{ 'page-button-container': true, active: activeTemplateID == child.ID }\"\n                            @click=\"changeActiveTemplateID(child.ID)\">\n                            <img class=\"template-image\" v-if=\"child.image\" :src=\"child.image\" :alt=\"child.name\" />\n                        </div>\n                        <p :class=\"{ 'template-small-header': true, active: activeTemplateID == child.ID }\"> {{ child.name }} </p>\n                    </div>\n                </div>\n            </div>\n        </div>\n\n    </div>\n    <CreateDialogFooter :cancel-button-cb=\"() => emits('close')\" :create-button-cb=\"create\"\n        :create-button-enabled=\"isNameValid\" />\n</template>\n<script setup lang=\"ts\">\nimport { IPageItem, IPageTemplate, ITemplateCategoryItem } from \"../../../../../../../common/qCloudTemp/template\";\nimport { computed, onBeforeMount, ref } from \"vue\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IModel } from \"../../../../../domain/model/models\";\nimport { ICreateModelParam, ICreateModel } from \"../../../../../domain/useCase/ICreateModel\";\nimport { IStudio } from \"../../../../../domain/useCase/IStudio\";\nimport { ITemplateUseCase } from \"../../../../../domain/useCase/ITemplateUseCase\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\nimport IDEInput from \"../../application/IDEInput.vue\";\nimport { IIDEInputDefinitions } from \"../../../../../domain/presentation/IIDEInputDefinitions\";\nimport CreateDialogFooter from \"./createDialogFooter.vue/createDialogFooter.vue\";\nimport path from \"path\";\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\nimport { IBackofficeUseCase } from \"../../../../../domain/useCase/backoffice/IBackofficeUseCase\";\nimport { IApplicationUseCase } from \"../../../../../domain/useCase/IApplicationUseCase\";\nimport { ICreateNewScreenProps } from \"../../IComponentProps\";\n\n\n// interface ISelectedTab {\n//     Web: string;\n//     Mobile: string;\n// }\nconst param = defineProps<{ param: ICreateNewScreenProps }>();\nconst props = param.param;\nconst emits = defineEmits([\"close\"]);\n\nconst studio = container.resolve<IStudio>(IStudio);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst templateUseCase = container.resolve<ITemplateUseCase>(ITemplateUseCase);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\nconst applicationUseCase = container.resolve<IApplicationUseCase>(IApplicationUseCase);\n\n\nconst templates = ref<Array<IPageTemplate>>([]);\nconst items = ref<Array<ITemplateCategoryItem>>([]);\nconst webItems = ref<Array<ITemplateCategoryItem | IPageItem>>([]);\nconst selectedItems = ref<Array<ITemplateCategoryItem | IPageItem>>([]);\nconst selectedTemplateCategory = ref(\"Web\")\n\n// var selectedTabName = ref(\"BLANK_PAGE\");\nconst selectedTabName = ref<Record<string, string>>({\"Web\": 'BLANK_PAGE', \"Mobile\": 'BLANK_PAGE'});\nconst name = ref(\"\");\nconst isNameValid = ref<boolean>(false);\nconst activeTemplateID = ref(\"BLANK_PAGE\");\nconst showWebTemplates = ref(true);\nconst showMobileTemplates = ref(false);\nconst currentApp = computed(() => viewModel.apps.find((app) => app.ID == viewModel.studio.appId));\n\n\nconst validations = ref<Record<\"name\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 2, max: 60 }, nonSpaceCharacter: true, customRegex:{ regex: /^([a-zA-Z0-9-_]+)$/, message: \"Available characters: A-Z, a-z, 1-9, '-' and '_'\"} }\n});\n\nonBeforeMount(async () => {\n    executor.execute(\n        async () => {\n            templates.value = await templateUseCase.listTemplateInfos(\"page\") as Array<IPageTemplate>;\n            items.value = convert(templates.value);\n            // webItems.value = items.value.find((i) => i.name == \"Web\")!.children;\n            selectedItems.value = items.value.find((i) => i.name == selectedTemplateCategory.value)!.children;\n            // selectedTabName.value = {Web: 'BLANK_PAGE', Mobile: 'BLANK_PAGE'}\n        },\n        { loading: true }\n    );\n});\nfunction selectTemplateCategory(type: string) {\n    switch(type) {\n        case \"Web\": showWebTemplates.value = true; showMobileTemplates.value = false; break;\n        case \"Mobile\": showWebTemplates.value = false; showMobileTemplates.value = true; break;\n    }\n    selectedTemplateCategory.value = type;\n    selectedItems.value = items.value.find((i) => i.name == selectedTemplateCategory.value)!.children;\n    changeMainTab(selectedTabName.value[type]);\n}\nconst convert = (items: Array<IPageTemplate>) => {\n    const seperateToGroups = (targetItems: Array<IPageTemplate>, field: \"category\" | \"subCategory\"): Record<string, IPageTemplate[]> => {\n        const groups: Record<string, Array<IPageTemplate>> = targetItems.reduce((group, item) => {\n            const category = item[field];\n            const grp = group as any;\n            grp[category] = grp[category] ?? [];\n            grp[category].push(item);\n            return grp;\n        }, {});\n        return groups;\n    };\n    const retVal: Array<ITemplateCategoryItem> = [];\n    const categoryGroups = seperateToGroups(items, \"category\");\n    Object.keys(categoryGroups).forEach((groupName) => {\n        const subGroupItems = templates.value.filter((i) => i.category == groupName);\n        const subGroups = seperateToGroups(subGroupItems, \"subCategory\");\n        const children: Array<ITemplateCategoryItem> = Object.keys(subGroups).map((i) => {\n            const subGroup = subGroups[i];\n            const subCategoryName = subGroup[0].subCategory;\n            const pageItems = subGroupItems.filter((i) => i.subCategory == subCategoryName);\n            const subCategory: ITemplateCategoryItem = {\n                name: subCategoryName,\n                type: \"category\",\n                children: pageItems.map((page) => {\n                    return <IPageItem>{ ID: page.ID, name: page.name, type: \"page\", image: page.image };\n                }),\n            };\n            return subCategory;\n        });\n        retVal.push({ name: groupName, type: \"category\", children });\n    });\n\n    return retVal;\n};\n\nfunction create() {\n    if (!isNameValid.value) return;\n    executor.execute(\n        async () => {\n            if (activeTemplateID.value == \"BLANK_PAGE\") {\n                const createObject: ICreateModelParam = {\n                    name: name.value,\n                    ownerId: props.ownerId,\n                    modelBody: [{ key: \"qjson\", model: \"\" }],\n                    modelType: \"qjson\",\n                    modelAdditionals: { qjsonType: \"qjson\" },\n                    path: props.path,\n                    version: \"\",\n                };\n                console.log(\"path\", props.path)\n                const createdModel = await executor.executeUseCase<ICreateModel>(ICreateModel, createObject);\n                await onAfterModelCreated(createdModel);\n            } else {\n                const createdModel = await templateUseCase.createModelFromTemplate({\n                    name: name.value,\n                    ownerId: props.ownerId,\n                    modelInfoID: activeTemplateID.value,\n                    modelAdditionals: { qjsonType: \"qjson\" },\n                    modelType: \"qjson\",\n                    path: props.path,\n                    version: \"\",\n                });\n                console.log(\"path\", props.path)\n                await onAfterModelCreated(createdModel);\n            }\n            const userMobileUsage = viewModel.user?.mobileUsage;\n            const appMobileUsage = currentApp.value?.mobileUsage;\n            if(selectedTemplateCategory.value == \"Mobile\" && (!userMobileUsage?.isMobilePublished || !appMobileUsage?.isMobilePublished)) {\n                backofficeUseCase.updateUserMobileUsage({...userMobileUsage, isMobileTemplateUsed: true});\n                applicationUseCase.updateApplication({ID: currentApp.value?.ID, mobileUsage: {...appMobileUsage, isMobileTemplateUsed: true}, updateReason: \"backofficeUpdate\"})\n            }\n            emits(\"close\");\n        },\n        { loading: true }\n    );\n}\n\nfunction changeActiveTemplateID(id: string) {\n    activeTemplateID.value = id;\n}\n\nfunction changeMainTab(selectedTab: string) {\n    selectedTabName.value[selectedTemplateCategory.value] = selectedTab;\n    const findCategoryItems = selectedItems.value.find(item => item.name === selectedTab) as ITemplateCategoryItem;\n    if(findCategoryItems) {\n        changeActiveTemplateID((<IPageItem>findCategoryItems.children[0]).ID)\n    } else {\n        changeActiveTemplateID(\"BLANK_PAGE\");\n    }\n\n}\n\nfunction flattener(categoryName: string): Array<IPageTemplate> {\n    var arrayFlattener: Array<any> = [];\n    const findCategoryItems = selectedItems.value.find(item => item.name === categoryName) as ITemplateCategoryItem;\n    if(findCategoryItems) {\n        arrayFlattener.push(findCategoryItems.children);\n    }\n    return arrayFlattener.flatMap((num) => num);\n}\n\nasync function onAfterModelCreated(createdModel: IModel) {\n    await studio.checkout(createdModel);\n    studio.openItem(createdModel);\n}\n\nfunction setNameValue(value: string) {\n    name.value = value;\n}\n\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid;\n}\n</script>\n\n<style scoped>\n@import url(\"../../../../../../src/styles/IDE3.css\");\n\n* {\n    font-family: \"Roboto\";\n}\n\n.blank-page{\n    position: absolute;\n    left: calc((100% - 68px)/2);\n    top: calc((100% - 27px - 90px)/2);\n}\n\n.dialog-container {\n    padding: 28px 20px 16px 26px;\n}\n\n.template-container {\n    margin-left: 29px;\n    position: relative;\n    overflow: hidden auto;\n    height: 464px;\n}\n\n.template-card-container {\n    width: 179px;\n    height: 220px;\n    margin-right: 20px;\n    margin-bottom: 20px;\n    position: relative;\n    float: left;\n}\n\n.template-image {\n    width: 170px;\n    height: 187px;\n    border-radius: 3px;\n}\n\n/* .template-container::-webkit-scrollbar {\n    width: 4px;\n}\n\n.template-container::-webkit-scrollbar-thumb {\n    background: #bcbcbc 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n}\n\n.template-container::-webkit-scrollbar-thumb:hover {\n    background: #555;\n} */\n\n.page-header-text {\n    color: #151233;\n    font-size: 14px;\n    font-weight: 500;\n}\n\n.name-bar {\n    width: 100%;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    padding: 10px 10px 11px;\n    margin-left: -6px;\n}\n\n.border-center {\n    border-left: 1px solid #d5d5d5;\n    height: calc(75vh - 263px);\n}\n\n.button-text-container {\n    margin-bottom: 16px;\n    color: #212121;\n    font-size: 16px;\n    font-weight: 400 bolder;\n    border: none;\n    background-color: white;\n    width: 138px;\n    height: 40px;\n    text-align: left;\n    border-radius: 4px;\n    padding-left: 10px;\n}\n\n.button-text-container:hover {\n    background-color: #e3e3e3;\n}\n\n.button-text-container.active {\n    background-color: #e3e3e3;\n}\n\n.bottom-side-container {\n    margin-top: 37px;\n}\n\n.cancel-button-container {\n    float: right;\n    padding: 10px 20px;\n    border-radius: 4px;\n    font-size: 15px;\n    font-weight: 500;\n    color: #757575;\n    background-color: white;\n    border: none;\n    margin: 16px 10px;\n}\n\n.create-button-contaner {\n    float: right;\n    padding: 10px 20px;\n    border-radius: 4px;\n    font-size: 15px;\n    font-weight: 500;\n    color: white;\n    background: #151233;\n    border: none;\n    margin: 16px 0px;\n}\n\n.page-button-container {\n    /* padding: 72px 30px; */\n    width: 177px;\n    height: 193px;\n    border-radius: 4px;\n    background-color: #eeeeee;\n    color: #d5d5d5;\n    border: 3px solid #eeeeee;\n}\n\n.page-button-container.active {\n    border: 3px solid #151233;\n}\n\n.template-small-header {\n    position: absolute;\n    top: 199px;\n    font: normal normal normal 16px/21px Roboto;\n    color: #616161;\n    width: 179px;\n    text-align: center;\n}\n.template-small-header.active {\n    color: #151233;\n}\n.template-tabs {\n    display: flex;\n    align-items: center;\n    width: 100%\n}\n.template-tabs div {\n    color: #A1A1A1;\n    font-weight: 500;\n    font-size: 14px;\n    width: 111px;\n    padding-bottom: 8px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    cursor: pointer;\n    border: 2px solid transparent;\n}\n.active-category {\n    color: #151233 !important; \n    border-bottom: 2px solid !important\n}\n.template-categories {\n    height: calc(75vh - 263px); \n    overflow: hidden auto;\n    padding-right: 20px;\n}\n.template-categories::-webkit-scrollbar-thumb {\n    background: #BDBDBD;\n    border-radius: 12px;\n    border: 6px solid #FFFFFF;\n    background-clip: padding-box;\n}\n.template-categories::-webkit-scrollbar-track {\n    background: #FFFFFF;\n}\n.template-categories::-webkit-scrollbar {\n    background: #FFFFFF;\n}\n</style>\n"],"sourceRoot":""}]);
